import { sendFeedback } from '@/api/index'
import { errorMsg } from '@/utils/common'
export default {
  data () {
    return {
      ask: ''
    }
  },
  computed: {
    openid () {
      return this.$store.state.user.openid
    }
  },
  methods: {
    // 去页面
    gotoPage (path) {
      this.$router.push({ path: path })
    },
    feedbackFn () {
      if (this.ask.trim() === '') {
        errorMsg(this, '不能为空')
        return
      }
      if (this.ask.trim().length < 10) {
        errorMsg(this, '圈子字数不能小于10个字')
        return
      }
      sendFeedback({
        openid: this.openid,
        ask: this.ask
      }).then(res => {
        this.gotoPage('/my')
      })
    }
  }
}



